// import Gitalk from 'gitalk'
import Crypto from "./plugins/crypto";
import Code from "./lib/CodeDialog";

import CollapseTransition from 'element-ui/lib/transitions/collapse-transition';
Vue.component(CollapseTransition.name, CollapseTransition)

let _axios = axios.create({withCredentials: true});
_axios.interceptors.request.use(config => {
  // config.headers['"User-Agent'] = 'httishere'
  // config.headers['Authorization'] = 'Bearer eyJ0eXBlIjoiSldUIiwiYWxnIjoiMjFkZWQ1Y2I1ZmY2YmEzNDA0MWQ3NDRiMTkzY2YzOWYifQ==.eyJleHAiOjE2MTAwMDc4NzIzNzIsImF2YWlsYWJsZSI6NDMyMDAwMDAwLCJodHRpc2hlcmUvaXNfcm9vdCI6dHJ1ZX0=';
  config.headers['X-Auth-Token'] = '8Bw0MWHoOQwNR1g1O8OtyLz5P0wyFnj8rkqlmOcD';
  return config
}, err => {
  return Promise.error(err);
})

Vue.use(VueClipboard)
Vue.prototype.$axios = _axios;
Vue.prototype.$global_path = process.env.NODE_ENV === "production" ? window.location.origin + '/widgets-site' : window.location.origin;

Vue.config.productionTip = false

const token_code = "724624a3c05c3a17e9d92a562cc39857"; // 32位加密
Vue.prototype.$token = token_code;

Vue.prototype.$crypto = Crypto;

Vue.prototype.$code = Code.install;

const whiteList = ["code", "index", "log", "lib", "reward", "lib", "time", "eff", "others", "picks", "paid"];

router.beforeEach((to, from, next) => {
  if(!whiteList.includes(to.name)) {
    // 需要验证口令
    let code = localStorage.getItem('token');
    if(code && code === token_code) next();
    else {
      Code.install({
        successGo() {
          next();
        }
      });
      next(false);
    }
  } else next();
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
