import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import FirstPanel from "./components/home/FirstPanel.vue";

Vue.use(Router);

export default new Router({
  // mode: 'history',
  // base: process.env.NODE_ENV === "production" ? '/notion-widgets-site/' : '/',
  routes: [
    {
      path: "/code",
      name: "code",
      component: () => import("./views/Code.vue"),
    },
    // { path: '*', component: NotFoundComponent },
    {
      path: "/",
      name: "home",
      component: Home,
      redirect: "/index",
      children: [
        {
          path: "/index",
          name: "index",
          component: FirstPanel,
        },
        {
          path: "/releases",
          name: "log",
          component: () =>
            import(/* webpackChunkName: "about" */ "./views/Log.vue"),
        },

        {
          path: "/reward",
          name: "reward",
          component: () =>
            import(/* webpackChunkName: "about" */ "./components/Reward.vue"),
        },
        {
          path: "/create/:id/:type",
          name: "create",
          component: () =>
            import(/* webpackChunkName: "about" */ "./views/Edit.vue"),
        },
        {
          path: "/news",
          name: "news",
          component: () => import("./views/News.vue"),
        },
        {
          path: "/picks",
          name: "picks",
          component: () => import("./views/Templates.vue"),
        },
        {
          path: "/picks/paid",
          name: "paid",
          component: () => import("./views/TempDetail.vue"),
        }
      ],
    },
    {
      path: "/lib",
      name: "lib",
      component: () => import("./views/Lib.vue"),
      redirect: "/lib/time",
      children: [
        {
          path: "/lib/time",
          name: "time",
          component: () =>
            import(/* webpackChunkName: "about" */ "./components/Times.vue"),
        },
        {
          path: "/lib/eff",
          name: "eff",
          component: () =>
            import(/* webpackChunkName: "about" */ "./components/Eff.vue"),
        },
        {
          path: "/lib/others",
          name: "others",
          component: () =>
            import(/* webpackChunkName: "about" */ "./components/Others.vue"),
        },
        {
          path: "/lib/formula",
          name: "formula",
          component: () =>
            import(/* webpackChunkName: "about" */ "./components/Formula.vue"),
        },
      ],
    },
    {
      path: "/wid/quote_pic/:mode?",
      name: "quote_pic",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "about" */ "./widget/QuoteWithPic.vue"),
    },
    {
      path: "/wid/title/:title?",
      name: "title",
      component: () =>
        import(/* webpackChunkName: "about" */ "./widget/Title.vue"),
    },
    {
      path: "/wid/history-today",
      name: "history-today",
      component: () =>
        import(/* webpackChunkName: "about" */ "./widget/HistoryToday.vue"),
    },
    {
      path: "/wid/today/:mode?",
      name: "today",
      component: () =>
        import(/* webpackChunkName: "about" */ "./widget/Today.vue"),
    },
    {
      path: "/wid/mul-days-matter",
      name: "mul-days-matter",
      component: () =>
        import(/* webpackChunkName: "about" */ "./widget/mul-days-matter.vue"),
    },
  ],
});
